.filter-sidebar {
    width: 345px;
    background: #111;
    color: white;
    overflow-y: auto;
}

.filter-sidebar.mini {
    width: 35px;
    overflow: hidden;
}

.filter-sidebar input[type=number] {
    margin: 0 10px;
    width: 70%;
}

.filter-sidebar .character-list {
    padding-top: 10px;
    list-style: none;
}

.character-list img {
    width: 60px;
}

.filter-sidebar h2 {
    word-wrap: break-word;
}

.filter-sidebar .search-button {
    margin: 30px;
    width: calc(100% - 60px);
    height: 40px;
}
