.App {
    text-align: center;
}

.App-logo {
    height: 40px;
}

.App-header {
    background-color: #222;
    top: 0px;
    height: 50px;
    width: 100%;
    padding: 0px;
    color: white;
    z-index: 100;
    overflow: hidden;
}

.App-header img {
    float: left;
    padding: 5px;
}

.App-header ul {
    list-style: none;
    margin: 0;
}

nav li {
    line-height: 110%;
}

.App-header ul li:hover {
    background: #f00;
}
.App-header a {
    float: right;
    color: white;
    text-decoration: none;
    padding-right: 10px;
    padding-left: 10px;
}
.App-header a:hover {
    background:#f00;
}

.App-header .App-title a:hover {
    background: none;
}

.App-header p {
    overflow: hidden;
}

.App-description ul {
    list-style: none;
}

.App-header .nav-item {
    font-weight: 600;
}

.App-header .nav-minor {
    font-size: 0.94em;
    font-weight: 500;
}

.App-header .App-title {
    font-size: 1.5em;
    font-weight: 600;
    float: left;
    margin-top: 18px;
}

.App-intro {
    font-size: large;
}

.StatsPage {
    background: #e1dcd2;
    height: calc(100vh - 50px);
}

.StatsPage-footer {
    position:fixed;
    left: 0;
    bottom: 0;
    z-index: 50;
}

.StatsPage ul {
    list-style-type: none;
    font-size: 2em;
    font-weight: 600;
    color: #3d403f;
    border: 3px solid #8b4f2b;
    margin-left: 110px;
    margin-right: 110px;
    padding-left: 0px;
    background: #8b4f2b;
    margin-top: 0px;
}

.StatsPage li {
    background: #dddfde;
    border: 3px solid #8b4f2b;
    border-radius: 7px;
    padding-top: 10px;
    padding-bottom: 10px;
    overflow: hidden;
}

.StatsPage-focused {
    position:absolute;
    border: 13px solid #f00;
    border-radius: 10px;
    pointer-events:none;
}

@keyframes App-logo-spin {
    from { transform: rotate(0deg); }
    to { transform: rotate(360deg); }
}


.char-container {
    display: flex;
    display: -webkit-flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    align-content: center;
}
.char-item {
    flex: 60 4 auto;
    margin: 5px;
}

.CharacterChart rect {
    fill:rgb(0,0,255);
    stroke:rgb(0,0,0);
}

rect:hover {
    /*fill:rgb(255, 0, 0);*/
}

.pie {
  width: 100px;
  border-radius: 50%;
  background:black;
  transform: rotate(-90deg);
}



.flextest {
    display: flex;
    flex-direction: row;
}

.fleximg {
    flex: 1;
}

.bg {
    fill:#262828;
}

.orange {
    fill:#e76a20;
    stroke-width: 0;
}


.StatsPage-gradient {
    position:absolute;
    left: 0;
    right: 0;
    top: 80vh;
    bottom: 5vh;
    pointer-events:none;
    background: -moz-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(224,221,213,1) 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(top, rgba(0,0,0,0) 0%,rgba(224,221,213,1) 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(224,221,213,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#e0ddd5',GradientType=0 ); /* IE6-9 */
}

.left {
    pointer-events:none;
    padding-left: 10vh;
    float: left;
}
.right {
    pointer-events:none;
    padding-right: 10vh;
    float: right;
    font-size: 1.15em;
    font-weight: 900;
}

.GameView .background {
    fill: #eee;
}


.GameView {
    display:block;
    overflow-y: auto;
}

.GameView line {
    stroke-width: 4;
}

.GameView line.major-grid {
    stroke-width: 5;
    stroke: #333;
}

.GameView line.minor-grid {
    stroke-width: 2;
    stroke: #777;
}

.GameView line.stock {
    stroke-dasharray: 10, 15;
    stroke-width: 2;
}

.GameView line.p1 {
    stroke: #f00;
}
.GameView line.p1.stock {
    stroke: #f55;
}
.GameView line.p2 {
    stroke: #00f;
}
.GameView line.p2.stock {
    stroke: #55f;
}
.GameView line.p3 {
    stroke: #bb0;
}
.GameView line.p3.stock {
    stroke: #dd5;
}
.GameView line.p4 {
    stroke: #0c0;
}
.GameView line.p4.stock {
    stroke: #5c5;
}

.GameView line.minor-grid-2 {
    stroke-width: 1;
    stroke: #999;
}

line.game-end {
    stroke-width: 3;
    stroke: #bbb;
}

line.percent-line {
    stroke-width: 4;
    stroke: #888;
}

line.stock-loss {
    stroke-width: 5;
    stroke: #f00;
}

.grid-stock-text {
    font-size: 17;
    font-weight: 900;
}

rect.bg {
    stroke:#fff;
    fill:#fff;
    fill-opacity: 0.4;
    stroke-opacity: 0.4;
}

rect.lag {
    fill:#f002;
}

.scroll {
    padding: 30px;
    min-width: 600px;
    height: 600px;
    overflow-x: scroll;
    overflow-y: hidden;
}


.App-description p {
    width: 60vw;
    padding-left: 20vw;
}

.App-description ul {
    padding-left: 0;
}

.mii-select {
    stroke: #000;
    stroke-width: 5;
    fill: none;
}

.red-team {
    stroke-width: 4;
    stroke: #f00;
}

.blue-team {
    stroke-width: 4;
    stroke: #00f;
}

.gray-team {
    stroke-width: 4;
    stroke: #aaa;
}


.TeamChart rect {
    fill: #00f;
}

.StatsPage-description {
    fill: #000;
    font-size: 2em;
    font-weight: 900;
}

.stat-value {
    text-anchor: end;
    font-size: 2.4em;
    font-weight: 900;
    fill: #e7eae7;
}

.stat-name {
    text-anchor: start;
    font-size: 2em;
    font-weight: 600;
    fill: #3d403f;
}

.stats-header {
    left: 0px;
    top: 50px;
}

.StatsPage-list {
    height: calc(100vh - (100vw/1920 * (100 + 229)) - 50px);
    overflow-y: scroll;
}


.fighter-placing {
    font-weight:600;
    fill: #c3c4c4;
}

.fighter-placing-alt {
    font-weight:500;
    fill: #a7a8a8;
}

.fighter-info {
    font-weight:700;
    font-size: 24px;
    fill: #354fb9;
}

.hover .fighter-info {
    fill: #a0200e;
}

.fighter-bar {
    fill: #354fb9;
    stroke-width:0;
}

.hover .fighter-bar {
    fill: #a0200e;
}

.fighter-base {
    fill: #2e2e2d;
    stroke-width: 0;
}

.hover .fighter-base {
    fill: #721018;
    stroke-width: 0;
}

.FighterStats {
    height: calc(100vh - 50px);
    overflow: hidden;
}

.MiiSelector {
    height: calc(100vh - 50px);
    display: flex;
    flex-direction: row;
}

.MiiView {
    display: flex;
    flex-wrap: wrap;
    flex: 9;
    background: #444;
    overflow-y: auto;
    align-content: flex-start;
    padding: 10px;
}

.highlight {
    background-color: #777;
    display: inline-block;
    position:relative;
    overflow: visible;
}
.highlight .mii-img:hover {
    opacity: 0.5;
}
.highlight .mii-img {
    opacity: 1;
    display: block;
    transition: opacity 0.2s;
    -webkit-transition: opacity 0.2s;
}
.highlight .name-img {
    position:absolute;
    width: 250px;
    top: 103%;
    left: -85px;
    display: inline;
    z-index: 3;
    background: #6087e7;
    color: #4b566b;
}

#TESTSPRITE {
    left: 0px;
    width: 200px;
    height: 200px;
    background: url("https://skydereign.github.com/forglory-data/chars/character_sprites.png") -100px 0;
}

.GameLead rect.p1 {
    fill: red;
}

.GameLead rect.p2 {
    fill: blue;
}

.GameLead {
    width: 100%;
}

.GameView {
    position: relative;
}

.for-hover {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
}

.checkbox {
    height: 19px;
}

.CheckboxLine {
    vertical-align: bottom;
}
.CheckboxLine span {
    padding-left: 10px;
}

label {
    padding-right: 10px;
}

.td-right {
    text-align: right;
    font-size: 0.9em;
}


.CharacterImg {
    position: relative;
    display: inline;
}

.CharacterImg .remove {
    position: absolute;    
    width: 10px;
    right: 0;
    top: 0;
    height: 10px;
    padding: 5px;
}

.CharacterImg .remove:hover {
    background: black;
}

.ListTable {
    text-transform: capitalize;
}

.center-popup {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
}

.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0,0,0, 0.7);
  z-index: 10;
}
.popup_fill {
  position: absolute;
  left: 5%;
  right: 5%;
  top: 5%;
  bottom: 5%;
  margin: auto;
}

.popup_inner {
    position: absolute;
    overflow-y: auto;
}


.tiers img {
    max-width: 75%;
}


.App-description h2 {
    margin-top: 50px;
}
.App-description a {
    display: inline;
}

.button-link {
    text-decoration: none;
    font-weight: 600;
}


.CharacterSelect.tiny img {
    width: 60px;
}

.CharacterSelect.tiny {
    display: flex;
    flex-wrap: wrap;
    width: 80%;
    height: 90vh;
}

.mii-list {
    display: flex;
    flex-wrap: wrap;
}
.mii-list .highlight {
    display: inline;
}

.FighterStats {
    background: rgb(36,36,36);
}

