.PlayerPopup {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    color: black;
}

.PlayerPopup .mii {
    flex-shrink: 0;
    padding: 20px;
}

.PlayerPopup .topbar {
    background: rgb(58, 80, 168);
    color: white;
    height: 100px;
    padding: 10px;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: row;

    border: 3px solid rgb(52, 63, 90);
}

.PlayerPopup .topbar .mii-name {
    border: 3px solid rgb(52, 63, 90);
}

.PlayerPopup {
    background: #fafafa;
}

.PlayerPopup .body {
    flex: 20;
    display: flex;
    flex-direction: row;
    overflow: hidden;
}

.PlayerPopup .games-list {
    list-style: none;
    padding: 0;
}

.games-list div {
    display: flex;
    align-items: center;
    padding: 5px 10px;
    justify-content: space-between;
}
.games-list .game-lost:hover, .games-list .game-won:hover {
    background: #ddd;
}

.games-list .focused .game-lost:hover {
    background: #a00;
}

.games-list .focused .game-won:hover {
    background: #070;
}

.games-list img {
    padding: 2px;
    width: 70px;
}

.games-list h3 {
    padding: 0 2px;
}

.PlayerPopup .game-view {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.PlayerPopup .win-rate {
    flex: 2;
}

.PlayerPopup .game-types {
    flex: 3;
    height: 100%;
    margin: 0 10px;
    display: flex;
    justify-content: space-around;
}

.game-types img {
    flex-grow: 0;
    padding: 10px;
}

.PlayerPopup .characters {
    flex: 8;
    flex-shrink: 0;
    height: 100%;
    list-style: none;
    
    overflow-x: auto;
    overflow-y: none;
    
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    padding: 0;
    overflow-y: hidden;
}

.characters img {
    padding: 0 5px;
    max-width: 90px;
    max-height: 90px;
}

.game-view .game-stats {
    font-weight: 600;
    flex: 1;
    display: flex;
    flex-wrap: wrap;
}


.game-stats .general {
    flex: 3;
}

.game-stats .p-info {
    flex: 4;
}

.game-stats .kills {
    flex: 3;
    padding: 15px;
}

.game-view .percent-container {
    flex: 2;
}

.p-info ul {
    list-style: none;
    padding-left: 0;
}

.game-stats table {
    width: 100%;
    padding: 0 10px;
    table-layout: fixed;
}

.game-stats h3 {
    margin: 10px 0;
}

.p-info h3 {
    margin: 10px 0;
}

.game-stats .img-c {
    flex: 1;
    align-self: flex-start;
    padding-top: 50px;
}

.img-c img {
    width: 50px;
    padding-bottom: 10px;
}

.focused .game-lost {
    background: darkred;
}

.focused .game-won {
    background: darkgreen;
}

.focused .game-dc {
    background: darkcyan;
}

.general, .p-info, .kills, .percent-container, .games-list {
    border: 2px solid #969696;
    background: white;
    margin: 8px; /* tweeked for small screens */
    padding: 8px;
    box-shadow: 0 0.1rem 0.4rem rgba(0,0,0,0.3);
    overflow-y: auto;
}

.games-list {
    background: #fff;
}

.header {
    padding: 0 40px;
}

li.game {
    padding: 0 0px;
}


.game-won {
    border-left: 6px solid green;
}
.game-lost {
    border-left: 6px solid red;
}
.game-dc {
    border-left: 6px solid cyan;
}

.game h3 {
    padding-right: 14px;
}

.PlayerPopupError {
    height: 100%;
    width: 100%;
    background: white;    
}



.PlayerPopup .topbar-mini {
    background: rgb(58, 80, 168);
    color: white;
    height: 60px;
    display: flex;
    flex-direction: row;
    border: 3px solid rgb(52, 63, 90);    
}

.PlayerPopup .topbar-mini h2 {
    padding: 0;
    margin: 0;
    font-size: 1em;
}

.PlayerPopup .topbar-mini img {
    padding: 0;
    width: 60px;
}

.PlayerPopup .topbar-mini .mii-name {
    width: 100%;
}

.kills table {
    width: 100%;
}

.games-list-container {
    height: 100%;
}

.games-list {
    overflow-y: auto;
    height: 100%;
}
